

.spinner-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  z-index: 999999;
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  background: #111;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

* {
    box-sizing: border-box;
  }


  .container-loader {
    position: relative;
    display: block;
    width: 70vh;
    height: 70vh;
    transform-style: preserve-3d;
    transform: perspective(1000px) rotateY(45deg) rotateX(45deg);
  }
  .circle:nth-child(0) {
    position: absolute;
    background: transparent;
    border: 2px solid #fff;
    border-radius: 50%;
    left: 0vh;
    top: 0vh;
    width: 70vh;
    height: 70vh;
    animation: spin Infinitys infinite linear;
  }
  .circle:nth-child(1) {
    position: absolute;
    background: transparent;
    border: 2px solid #fff;
    border-radius: 50%;
    left: 2.1875vh;
