  .circle:nth-child(2n) {
    border: 2px dashed rgba(255,255,255,0.5);
  }
  .circle:last-child {
    display: none;
  }
  @-moz-keyframes spin {
    0% {
      transform: rotateX(0deg);
    }
    100% {
      transform: rotateX(360deg);
    }
  }
  @-webkit-keyframes spin {
    0% {
      transform: rotateX(0deg);
    }
    100% {
      transform: rotateX(360deg);
    }
  }
  @-o-keyframes spin {
